"use client"

import { useState, useEffect, useCallback } from "react"
import { Zap, LogOut, LogIn, Cloud, CloudOff } from "lucide-react"
import { Button } from "@/components/ui/button"
import { ChargingHistory } from "@/components/charging-history"
import { AddChargeDialog } from "@/components/add-charge-dialog"
import { EditSessionDialog } from "@/components/edit-session-dialog"
import {
  getLocalSessions,
  addLocalSession,
  updateLocalSession,
  deleteLocalSession,
  type ChargingSession,
} from "@/lib/storage"
import { createClient } from "@/lib/supabase/client"
import type { User } from "@supabase/supabase-js"
import Link from "next/link"

export function ChargingTracker() {
  const [user, setUser] = useState<User | null>(null)
  const [sessions, setSessions] = useState<ChargingSession[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedSession, setSelectedSession] = useState<ChargingSession | null>(null)
  const [editDialogOpen, setEditDialogOpen] = useState(false)
  const supabase = createClient()

  const loadSessions = useCallback(async () => {
    if (user) {
      // Load from Supabase for authenticated users
      const { data } = await supabase.from("charging_sessions").select("*").order("charged_at", { ascending: false })
      setSessions(data || [])
    } else {
      // Load from localStorage for guests
      setSessions(getLocalSessions())
    }
  }, [user, supabase])

  useEffect(() => {
    // Check auth state
    const checkUser = async () => {
      const {
        data: { user },
      } = await supabase.auth.getUser()
      setUser(user)
      setLoading(false)
    }
    checkUser()

    // Listen for auth changes
    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      setUser(session?.user ?? null)
    })

    return () => subscription.unsubscribe()
  }, [supabase])

  useEffect(() => {
    if (!loading) {
      loadSessions()
    }
  }, [loading, user, loadSessions])

  const handleAddSession = async (data: {
    cost: number
    startPercent: number
    endPercent: number
    kwh?: number
    chargeType?: "fast" | "standard"
  }) => {
    if (user) {
      // Save to Supabase
      const { error } = await supabase.from("charging_sessions").insert({
        cost: data.cost,
        start_percent: data.startPercent,
        end_percent: data.endPercent,
        kwh: data.kwh || null,
        charge_type: data.chargeType || null,
        user_id: user.id,
      })
      if (!error) {
        loadSessions()
      }
      return { error: error?.message }
    } else {
      // Save to localStorage
      addLocalSession({
        cost: data.cost,
        start_percent: data.startPercent,
        end_percent: data.endPercent,
        kwh: data.kwh || null,
        charge_type: data.chargeType || null,
      })
      loadSessions()
      return {}
    }
  }

  const handleEditSession = async (
    id: string,
    updates: {
      cost: number
      start_percent: number
      end_percent: number
      kwh?: number | null
      charge_type?: "fast" | "standard" | null
      charged_at: string
    },
  ) => {
    if (user) {
      // Update in Supabase
      const { error } = await supabase.from("charging_sessions").update(updates).eq("id", id)

      if (!error) {
        loadSessions()
      }
      return { error: error?.message }
    } else {
      // Update in localStorage
      updateLocalSession(id, updates)
      loadSessions()
      return {}
    }
  }

  const handleDeleteSession = async (id: string) => {
    if (user) {
      // Delete from Supabase
      const { error } = await supabase.from("charging_sessions").delete().eq("id", id)

      if (!error) {
        loadSessions()
      }
      return { error: error?.message }
    } else {
      // Delete from localStorage
      deleteLocalSession(id)
      loadSessions()
      return {}
    }
  }

  const handleSessionClick = (session: ChargingSession) => {
    setSelectedSession(session)
    setEditDialogOpen(true)
  }

  const handleLogout = async () => {
    await supabase.auth.signOut()
    setUser(null)
    setSessions(getLocalSessions())
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <Zap className="h-8 w-8 animate-pulse text-primary" />
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-background via-background to-primary/5">
      <header className="border-b border-border/50 backdrop-blur-sm bg-background/80 sticky top-0 z-10">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="relative">
                <Zap className="h-7 w-7 text-primary drop-shadow-[0_0_8px_rgba(79,124,255,0.5)]" />
              </div>
              <div>
                <h1 className="text-xl font-bold bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text">
                  EV Charging
                </h1>
                <p className="text-xs text-muted-foreground">Track your costs</p>
              </div>
            </div>
            <div className="flex items-center gap-2">
              {user ? (
                <>
                  <span className="text-xs text-muted-foreground flex items-center gap-1">
                    <Cloud className="h-3 w-3" />
                    Synced
                  </span>
                  <Button variant="outline" size="sm" onClick={handleLogout}>
                    <LogOut className="h-4 w-4" />
                  </Button>
                </>
              ) : (
                <>
                  <span className="text-xs text-muted-foreground flex items-center gap-1">
                    <CloudOff className="h-3 w-3" />
                    Local
                  </span>
                  <Link href="/auth/login">
                    <Button variant="outline" size="sm">
                      <LogIn className="h-4 w-4 mr-1" />
                      Sync
                    </Button>
                  </Link>
                </>
              )}
            </div>
          </div>
        </div>
      </header>
      <main className="container mx-auto px-4 py-6 pb-24">
        <ChargingHistory sessions={sessions} onSessionClick={handleSessionClick} />
      </main>
      <AddChargeDialog onAdd={handleAddSession} />
      <EditSessionDialog
        session={selectedSession}
        open={editDialogOpen}
        onOpenChange={setEditDialogOpen}
        onSave={handleEditSession}
        onDelete={handleDeleteSession}
      />
    </div>
  )
}
